.add{
    margin: 15px auto;
    padding: 15px;
    width: calc(100% - 30px);
    max-width: 300px;
    &__heading{
        text-align: center;
        font-size: 1.5rem;
        margin: 10px 0;
    }
    &__message{
        color: white;
        padding: 10px;
        margin: 10px 0;
        text-align: center;
        &--success{
            background-color: lightgreen;
        }
        &--error{
            background-color: crimson;
        }
    }
    &__form{
        display: flex;
        flex-direction: column;
        gap: 5px;
        &__input{
            padding: 10px;
            -moz-appearance: textfield;
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }
        &__button{
            padding: 10px;
            cursor: pointer;
        }
    }
}